
@import url(./animations.css);
#app {
  font-family: 'Nunito', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
html, body {
  margin: 0;
  padding: 0;
}
.main {
  -webkit-animation: fade .3s ease;
          animation: fade .3s ease;
}
.main.config:before {
    content: "";
    background: #a1b323;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999999;
    opacity: .7;
    -webkit-animation: fade .3s ease;
            animation: fade .3s ease;
}



